.header__link {
  color: #A9A9A9;
  text-align: right;
  font-family: 'Inter', 'Arial', 'Helvetica', sans-serif;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 22px;
  transition: opacity 0.5s;
  text-decoration: none;
}

.header__link:hover {
  opacity: 0.6;
}