.popup {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: rgba(0, 0, 0, .5);
  z-index: 9999;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.15s, opacity 0.15s linear;
}
